<template>
  <form @submit.prevent="sendEmail">
      <div class="input-group">
        <label>Nombre:</label>
        <input
        type="text" 
        placeholder="Gino, from Google"
        name="name" 
        :class="{inputError: errors.wrongName}"
        v-model="name"    
        />
      </div>
      <div class="input-group">
        <label>Asunto:</label>
        <input
        type="text" 
        placeholder="Oferta..."
        name="subjet" 
        :class="{inputError: errors.wrongSubjet}"
        v-model="subjet"    
        />
      </div>
      <div class="input-group">
        <label>Email:</label>
        <input
        type="email" 
        placeholder="mail@mail.com"
        name="email" 
        :class="{inputError: errors.wrongEmail}"
        v-model="email"    
        />
      </div>
      <div class="input-group">
        <label>Mensaje:</label>
        <textarea
          type="text"
          placeholder="Mensaje..."
          name="message"          
          :class="{inputError: errors.wrongMessage}"                  
          v-model="message" 
          >
          </textarea>
      </div>
      <div class="input-group">
        <button type="submit" value="Send">Enviar</button>
      </div>
  </form>
</template>

<script>
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import emailjs from 'emailjs-com';

export default {
  data() {
    return {
      message: '',
      name: '',
      email: '',
      subjet: '',
      errors: [
        {wrongEmail: false},
        {wrongName: false},
        {wrongSubjet: false},
        {wrongMessage: false}
      ],
    };
  },
  mounted() {
    gsap.registerPlugin(ScrollTrigger);

    gsap.utils.toArray(".input-group").forEach((cardE) => {
      gsap.from(cardE, {
        scrollTrigger: {
          trigger: cardE,
          start: "0 100%",
          toggleActions: "play none none none",
        },
        y: 50,
        duration: 0.9,
        opacity: 0,
      });
    });
  },
  methods: {    
    sendEmail(e) {
      if(this.name.trim().replace(/(\r\n|\n|\r)/gm, "") !== ""){
        this.errors.wrongName =false;
      }  
      if(this.subjet.trim().replace(/(\r\n|\n|\r)/gm, "") !== ""){
        this.errors.wrongSubjet =false;
      }   
      if(this.email.trim().replace(/(\r\n|\n|\r)/gm, "") !== ""){
        this.errors.wrongEmail = false;
      }
      if(this.message.trim().replace(/(\r\n|\n|\r)/gm, "") !== ""){
        this.errors.wrongMessage =false;
      }
      if (this.name.trim().replace(/(\r\n|\n|\r)/gm, "") === "") {
        this.errors.wrongName = true;
      }
      if (this.subjet.trim().replace(/(\r\n|\n|\r)/gm, "") === "") {
        this.errors.wrongSubjet = true;
      }
      if (this.email.trim().replace(/(\r\n|\n|\r)/gm, "") === "") {
        this.errors.wrongEmail = true;
      }
      if (this.message.trim().replace(/(\r\n|\n|\r)/gm, "") === "") {
        this.errors.wrongMessage =true;
      }
      if (this.name.trim().replace(/(\r\n|\n|\r)/gm, "") !== "" && 
        this.email.trim().replace(/(\r\n|\n|\r)/gm, "") !== "" && 
        this.message.trim().replace(/(\r\n|\n|\r)/gm, "") !== "" &&
        this.subjet.trim().replace(/(\r\n|\n|\r)/gm, "") !== "") {
        emailjs.sendForm('service_rpqnlf4', 'template_06euihp', e.target, '7rbPhdBu71nb8XZWr')
        .then((result)=> {
          console.log(result.text);

        })
        .catch((error)=>{console.log(error);          
        });
        this.name = '';
        this.email = '';
        this.message='';
        this.subjet='';
        // Show feedback
      }
    },
  },
};
</script>

<style scoped>
 
form {
  margin: auto;
  font-size: 1.6rem;
  text-align: left;
  padding: 0;
  display: flex;
  flex-direction: column;
  max-width: 1000px;
}
.input-group {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: inherit;
}
label {
  margin: 0 0 0 1rem;
  padding: 0 0 0 1rem;
  flex-basis: 15%;
}
input,
textarea, button {
  padding: 1rem;
  margin: 1rem 1.5rem;
  font-size: inherit;
  background-color: #ffffff;
  border: 1px solid var(--dark-2-color);
  border-radius: 5px;
  flex: auto;
  resize: vertical;
  outline: none;
  font-family:inherit;
}
input:focus,
textarea:focus,
button:hover {
  outline: none;
  box-shadow: 0px 0px 3px 1px var(--dark-color);
}
.dark-mode input,
.dark-mode textarea, 
.dark-mode button{
  background-color: var(--dark-2-color);
  color: var(--light-2-color);
}
.dark-mode input:focus,
.dark-mode textarea:focus,
.dark-mode button:hover {
  outline: none;
  box-shadow: 0px 0px 3px 1px var(--light-color);
}
.inputError {
  border: 1px solid var(--error-color) !important;
}
.inputError:focus{
  box-shadow: 0px 0px 3px 1px var(--error-color) !important;
}
@media (max-width: 850px) {
  .input-group {
    flex-direction: column;
    align-items: inherit;
  }
  label {
    margin: 0;
    text-align: left;    
  }
}

</style>